@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.preloader {
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 3;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.preloader__layout {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #555;
  opacity: .8;
}

.preloader__spiner {
  font-size: 40px;
  border: 1px #FFF solid;
  border-radius: 130px;
  width: 250px;
  height: 250px;
  animation-name: rotation;
  animation-duration: 4s;
  animation-iteration-count: infinite;
  animation-timing-function: ease;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

@media screen and (max-width: 400px) {
  .preloader__spiner {
    font-size: 30px;
    width: 180px;
    height: 180px;
  }
}
